  changeDate(_date) { 
    if (_date) {
      var value = new Date(_date);
      var year = value.getFullYear()
      var month = value.getMonth() + 1
      var day = value.getDate()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return [year, month, day].join('-')
    }
    return '';
  },
