  //
  getBrowserAgentr() {
    var agent = navigator.userAgent.toLowerCase();
    var sUserAgent = navigator.userAgent;
    var regStr_ie = /msie [\d.]+;/gi;
    var regStr_ff = /firefox\/[\d.]+/gi
    var regStr_chrome = /chrome\/[\d.]+/gi;
    var regStr_saf = /safari\/[\d.]+/gi;
    //IE  
    if (agent.indexOf("msie") > 0) {
      return 'IE'
    } else if (
      (agent.indexOf("windows nt") > -1 && agent.indexOf("win64") > -1) ||
      (agent.indexOf("windows nt") > -1 && agent.indexOf("win32") > -1)
    ) {
      return 'Edge'
    } else if (agent.indexOf('rv') > 0) {
      if (Math.abs(agent.match(/rv:(\d+\.\d+)/)[1]) == 11) {
        return 'IE'
      }
    } else if (agent.indexOf("firefox") > 0) {
      return 'firefox'
    } else if (agent.indexOf("chrome") > 0) {
      if (agent.indexOf("360ee") > 0) {
        return '360'
      }
      return 'chrome'
    } else if (agent.indexOf("safari") > 0 && agent.indexOf("chrome") < 0) {
      return 'safari'
    }
  },
  //
  getBrowOs() {
    var sUserAgent = navigator.userAgent;
    var isWin = (navigator.platform == "Win32") || (navigator.platform == "Windows");
    var isMac = (navigator.platform == "Mac68K") || (navigator.platform == "MacPPC") || (navigator.platform == "Macintosh") || (navigator.platform == "MacIntel");
    if (isMac) return "Mac";
    var isUnix = (navigator.platform == "X11") && !isWin && !isMac;
    if (isUnix) return "Unix";
    var isLinux = (String(navigator.platform).indexOf("Linux") > -1);
    var bIsAndroid = sUserAgent.toLowerCase().match(/android/i) == "android";
    if (isLinux) {
      if (bIsAndroid) return "Android";
      else return "Linux";
    }
    if (isWin) {
      var isWin2K = sUserAgent.indexOf("Windows NT 5.0") > -1 || sUserAgent.indexOf("Windows 2000") > -1;
      if (isWin2K) return "Win2000";
      var isWinXP = sUserAgent.indexOf("Windows NT 5.1") > -1 || sUserAgent.indexOf("Windows XP") > -1
      sUserAgent.indexOf("Windows XP") > -1;
      if (isWinXP) return "WinXP";
      var isWin2003 = sUserAgent.indexOf("Windows NT 5.2") > -1 || sUserAgent.indexOf("Windows 2003") > -1;
      if (isWin2003) return "Win2003";
      var isWinVista = sUserAgent.indexOf("Windows NT 6.0") > -1 || sUserAgent.indexOf("Windows Vista") > -1;
      if (isWinVista) return "WinVista";
      var isWin7 = sUserAgent.indexOf("Windows NT 6.1") > -1 || sUserAgent.indexOf("Windows 7") > -1;
      if (isWin7) return "Win7";
      var isWin8 = sUserAgent.indexOf("Windows NT 6.2") > -1 || sUserAgent.indexOf("Windows 8") > -1;
      if (isWin8) return "Win8";
      var isWin10 = sUserAgent.indexOf("Windows NT 10.0") > -1 || sUserAgent.indexOf("Windows 10") > -1;
      if (isWin10) return "Win10";
    }
    return "";
  },
  //cookies
  setCookie(name, value) {
    var Days = 30;
    var exp = new Date();
    exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
    document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString() + 'path=/';
  },
  
  setNewCookie(name, value, time) {
    var Days = !!time ? time : 30;
    var exp = new Date();
    exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
    document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString() + 'path=/';
  },
  
  //cookie
   getCookies(name) {
    var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    if (arr = document.cookie.match(reg))
      return unescape(arr[2]);
    else
      return null;
  },
  
  //cookie
  delCookie(name) {
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval = getCookie(name);
    if (cval != null)
      document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
  },
  
  //ie
  IeVesion() {
    var Sys = {};
    var ua = navigator.userAgent.toLowerCase();
    var s;
    (s = ua.match(/msie ([\d.]+)/)) ? Sys.ie = s[1]:
      (s = ua.match(/firefox\/([\d.]+)/)) ? Sys.firefox = s[1] :
      (s = ua.match(/chrome\/([\d.]+)/)) ? Sys.chrome = s[1] :
      (s = ua.match(/opera.([\d.]+)/)) ? Sys.opera = s[1] :
      (s = ua.match(/version\/([\d.]+).*safari/)) ? Sys.safari = s[1] : 0;
    var ieversion;
    if (Sys.ie) {
      ieversion = parseInt(Sys.ie);
      if (ieversion < 8) {
        alert("\n\n Internet Explorer:" + Sys.ie + "");
        setIsTextReadOnly(true);
      }
    } else if (Sys.firefox) {
      ieversion = Sys.firefox;
      //setIsTextReadOnly(true); 
    } else if (Sys.chrome) {
      ieversion = Sys.chrome;
    } else if (Sys.opera) {
      ieversion = Sys.opera;
    } else if (Sys.safari) {
      ieversion = Sys.safari;
    }
  },
  //+
   getExplorerInfo() {
    var explorer = window.navigator.userAgent.toLowerCase();
    //ie 
    if (explorer.indexOf("msie") >= 0) {
      var ver = explorer.match(/msie ([\d.]+)/)[1];
      return {
        type: "IE",
        version: ver
      };
    }
    //firefox 
    else if (explorer.indexOf("firefox") >= 0) {
      var ver = parseInt(explorer.match(/firefox\/([\d.]+)/)[1]);
      return {
        type: "Firefox",
        version: ver
      };
    }
    //Chrome
    else if (explorer.indexOf("chrome") >= 0) {
      var ver = parseInt(explorer.match(/chrome\/([\d.]+)/)[1]);
      return {
        type: "Chrome",
        version: ver
      };
    }
    //Opera
    else if (explorer.indexOf("opera") >= 0) {
      var ver = parseInt(explorer.match(/opera.([\d.]+)/)[1]);
      return {
        type: "Opera",
        version: ver
      };
    }
    //Safari
    else if (explorer.indexOf("Safari") >= 0) {
      var ver = parseInt(explorer.match(/version\/([\d.]+)/)[1]);
      return {
        type: "Safari",
        version: ver
      };
    }
  }
  
